* {
  font-family: Calibri, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: orangered;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid lightgrey;
  border-radius: 8px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  margin: 0px;
  background-color: lightgrey;
  color: darkblue;
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}